body {
	overflow-x: hidden; }
main {
	padding: 90px 0 0; }

@keyframes load {
	0% {
		opacity: 0; }
	100% {
		opacity: 1; } }

@keyframes load2 {
	0% {
		width: 100%; }
	100% {
		width: 0; } }

.nav {
	animation: load 1.5s linear forwards;
	animation-delay: 1s;
	opacity: 0;
	position: fixed;
	z-index: 1;
	ul {
		list-style: none;
		padding: 0;
		li {
			margin-bottom: 12px;
			&:last-child {
				margin-bottom: 0; }
			a {
				color: $gray;
				font-family: $UM;
				font-size: 10px;
				font-weight: 500;
				letter-spacing: 0.25px;
				padding: 6px 20px 6px 0;
				text-decoration: none;
				transition: 0.2s;
				position: relative;
				span {
					position: relative; }
				&:before {
					position: absolute;
					content: '';
					top: 0;
					width: 0;
					height: 100%;
					background: $yellow;
					transition: 0.2s; }
				&:hover {
					color: $title;
					&:before {
						width: 100%; } } } } }
	&-active {
		a {
			background: $red;
			color: $white !important;
			&:before {
				display: none; }
			&:hover {
				background: $red !important; } } } }
.content {
	width: 80%;
	margin: 0 0 0 auto; }
.btn {
	display: inline-block;
	text-align: center;
	color: $black;
	background: $btn;
	font-family: $UB;
	font-size: 12px;
	font-weight: 700;
	letter-spacing: 0.24px;
	padding: 20px 100px;
	text-decoration: none;
	transition: 0.2s;
	position: relative;
	span {
		position: relative; }
	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 0;
		height: 100%;
		background: rgb(249, 180, 88);
		transition: all 0.4s ease; }
	&:hover {
		// background: #f9b458
		&:before {
			width: 100%; } } }
