.checkbox {
	margin-top: 20px;
	input {
		display: none;
		&:checked + label:after {
			display: block; } }
	label {
		display: inline-block;
		color: $title;
		font-family: $UB;
		font-size: 14px;
		font-weight: 700;
		letter-spacing: 0.35px;
		padding-left: 30px;
		cursor: pointer;
		position: relative;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
			border: 2px solid #656565;
			background-color: #f7f7f7;
			box-sizing: border-box;
			border-radius: 50%; }
		&:after {
			display: none;
			position: absolute;
			content: '';
			top: 50%;
			left: 5px;
			transform: translateY(-50%);
			width: 10px;
			height: 10px;
			background-color: #f9b458;
			border-radius: 50%; } } }

.range-slider {
	width: 450px; }
.rs-range {
	width: 100%;
	-webkit-appearance: none;
	&:focus {
		outline: none; }

	&::-webkit-slider-runnable-track {
		height: 3px;
		cursor: pointer;
		box-shadow: none;
		background: $title; }

	&::-moz-range-track {
		width: 450px;
		height: 3px;
		cursor: pointer;
		box-shadow: none;
		background: $title; }

	&::-webkit-slider-thumb {
		width: 21px;
		height: 20px;
		border: 2px solid #2b2c28;
		background-color: #f9b458;
		border-radius: 50%;
		-webkit-appearance: none;
		margin-top: -9px; }

	&::-moz-range-thumb {
		width: 21px;
		height: 20px;
		border: 2px solid #2b2c28;
		background-color: #f9b458;
		border-radius: 50%;
		-webkit-appearance: none;
		margin-top: -9px; }

	&::-moz-focus-outer {
		border: 0; } }

