@keyframes thisSlide {
	0% {
		width: 100%; }
	100% {
		width: 0%; } }

@keyframes nextSlide {
	0% {
		width: 0%; }
	100% {
		width: 100%; } }

.this-slide {
	animation: thisSlide 0.5s ease;
	margin: 0 auto 0 0; }

.next-slide {
	animation: nextSlide 0.5s ease;
	margin: 0 0 0 auto; }

